@keyframes animationClick {
    0% {
        transform: scale(0.8);
    } 50% {
        transform: scale(0.9);
    } 100% {
        transform: scale(1);
    }
}

#home {
    padding: 2em;
    display: flex;
    flex-direction: column;
    row-gap: 1.5em;
    width: 100%;

    #not-faund {
        text-align: center;
    }

    #nav-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: 2em;
        
        input {
            width: 100%;
            height: 2em;
            padding: 1em;
            border: 1px solid rgba(0, 0, 0, 0.400);
            border-radius: 1em;
            text-decoration: none;
            outline: none;
        }
    
        #menu {
            display: inline-block;
            cursor: pointer;
    
            div {
                width: 30px;
                height: 4px;
                background-color: #333;
                margin: 5px 0;
                transition: 0.4s;
            }
        }
    }

    #cards {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(190px, 1fr));
        gap: 2em;
    
        .card {
            height: calc(120px * 1.3);
            animation: 1s ease-in-out;
            border-radius: 1em;
            padding: 0.5em;
            display:  flex;
            justify-content: left;
            position: relative;
    
            img {
                position: absolute;
                transform: scale(1.3);
                bottom: 0;
                right: 0;
            }
    
            h1 {
                font-size: 1.8em;
                text-transform: capitalize;
                font-weight: 400;
                text-align: left;
                color: white;
            }
    
            &.card:hover {
                transform: scale(1.02);
            }
    
            &.card:active{
                animation: animationClick 1000000000000000000000000000000000000s none;
            }
        }

        .card:nth-child(3n) img {
            margin: 3px;
        }
    }

    .change {
        .bar1 {transform: translate(0, 9px) rotate(-45deg);}
        .bar2 {opacity: 0;}
        .bar3 {transform: translate(0, -9px) rotate(45deg);}
    }
     
    #carregar {
        margin: 1em;
        text-align: center;
    }
}